<template>
  <div>
    <el-container style="border: 1px solid #eee;">
      <el-header height="30px" class="table-header">
          <div></div>
      </el-header>
      <el-main style="padding: 0px;">
        <div style="padding-left: 10px;text-align: left;border-bottom: 1px solid #eee;">
          <el-button type="text" class="el-icon-plus" @click="tmAddFunc"></el-button>
          <el-button type="text" class="el-icon-edit" @click="tmEditFunc"></el-button>
          <el-button type="text" class="el-icon-delete" @click="tmDelFunc"></el-button>
          <el-button type="text" class="el-icon-search" @click="tmInfoFunc"></el-button>
        </div>
        <el-table :data="tmTableData" ref="tmTable"
                  :span-method="groupHandle"
                  :row-class-name="groupClassName"
                  @row-dblclick="toggleListHandle"
                  max-height="430"
                  style="width: 100%">
          <el-table-column type="selection" width="35" align="left"></el-table-column>
          <el-table-column label="" prop="name">
            <template slot-scope="scope">
              <i v-if="scope.row.group_type==='group'" :class=groupIcon(scope.row)></i>
              <span style="margin-left: 10px">{{scope.row.name}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="ref_name"></el-table-column>
          <el-table-column label="" prop="src_id"></el-table-column>
          <el-table-column label="" prop="type"></el-table-column>
          <el-table-column label="" prop="fd_name"></el-table-column>
          <el-table-column label="" prop="code" :formatter="formatterCode"></el-table-column>
          <el-table-column label="" prop="src_default"></el-table-column>
          <el-table-column label="" prop="genesisrul"></el-table-column>
        </el-table>
      </el-main>
    </el-container>
    <el-dialog :title="tmTitle" :visible.sync="tmDialogVisible" @open="openTmDialog">
      <el-form :model="tmForm" style="text-align: left;">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="tmForm.tab_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="tmForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="tmForm.ref_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="tmForm.src_id" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="tmForm.type" placeholder="">
            <el-option
              v-for="item in tmTypeList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="tmForm.fd_name" placeholder="">
            <el-option
              v-for="item in tmFdNameList"
              :key="item.fd_name"
              :label="item.fd_name"
              :value="item.fd_name">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="tmForm.code" placeholder="">
            <el-option
              v-for="item in tmCodeList"
              :key="item.category_id"
              :label="item.category_name"
              :value="item.category_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="tmForm.src_default" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="tmForm.genesisrul" placeholder="">
            <el-option
              v-for="item in tmGenesisRulList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="tmDialogVisible = false"> </el-button>
        <el-button type="primary" @click="tmDialogVisible = false"> </el-button>
      </div>
    </el-dialog>
    <el-container style="border: 1px solid #eee;">
      <el-header height="30px" class="table-header">
        <div></div>
      </el-header>
      <el-main style="padding: 0px;">
        <div style="padding-left: 10px;text-align: left;border-bottom: 1px solid #eee;">
          <el-button type="text" class="el-icon-plus" @click=""></el-button>
          <el-button type="text" class="el-icon-edit" @click=""></el-button>
          <el-button type="text" class="el-icon-delete" @click=""></el-button>
          <el-button type="text" class="el-icon-plus" @click=""></el-button>
          <el-button type="text" class="el-icon-edit" @click=""></el-button>
          <el-button type="text" class="el-icon-delete" @click=""></el-button>
          <el-button type="text" class="el-icon-search" @click=""></el-button>
        </div>
        <el-table :data="refTableData" ref="refTable"
                  :span-method="groupHandle"
                  :row-class-name="groupClassName"
                  @row-dblclick="toggleListHandle"
                  max-height="430"
                  style="width: 100%">
          <el-table-column type="selection" width="35" align="left"></el-table-column>
          <el-table-column label="" prop="ref_desc">
            <template slot-scope="scope">
              <i v-if="scope.row.group_type==='group'" :class=groupIcon(scope.row)></i>
              <span style="margin-left: 10px">{{scope.row.ref_desc}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="ref_name"></el-table-column>
          <el-table-column label="" prop="ref_fd_name"></el-table-column>
          <el-table-column label="" prop="src_cond_in"></el-table-column>
          <el-table-column label="" prop="src_expr_in"></el-table-column>
          <el-table-column label="" prop="storecolumn"></el-table-column>
          <el-table-column label="" prop="tab_desc"></el-table-column>
        </el-table>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import ajax from '@/common/ajax'
  import dictCode from '@/common/dictCode'

  let vm = null
  export default {
    create () {

    },
    computed: {
      txnIdParent () { return this.txnId }, // 
      isVisibilityParent () { return this.isVisibility }, // tab
      tmTableData () { // Table
        var expendNodeKey = this.expendNodeKey
        var tranModelList = this.tranModelList
        var tableData = []
        for (var i in tranModelList) {              // 
          for (var key in tranModelList[i]) {       // Key-Value
            if (tranModelList[i][key].length > 0) { // ValueList
              let keys = key.split('____')          // 
              if (keys && keys.length === 2) {      // 
                tableData.push({                    // 
                  tab_name: keys[0],
                  name: keys[1],
                  group_type: 'group'                // [group]
                })
                if (expendNodeKey.indexOf(key) === -1) {  // 
                  break                                   // 
                }
                for (var j in tranModelList[i][key]) {    // 
                  var item = tranModelList[i][key][j]
                  item.group_type = 'item'                // [item]
                  tableData.push(item)
                }
              }
            }
          }
        }
        return tableData
      },
      tmFdNameList () { // 
        let self = this
        let dataType = self.tmForm.type
        let enableStoreFd = self.enableStoreFd
        let dataTypeClassify = self.dataTypeClassify
        var sfdItems = []
        if (dataType && enableStoreFd) {
          for (let i in enableStoreFd) {
            let fd = enableStoreFd[i]
            for (let d in dataTypeClassify) {
              let dt = dataTypeClassify[d]
              if (dt.type.indexOf(dataType) !== -1) {
                if (dt.recap === 'long' || dt.recap === 'decimal') {
                  if (dataType !== 'double' && dataType !== 'long') {
                    if (fd['type'] === dataType) {
                      sfdItems.push(fd)
                    }
                  }
                  if (fd['type'] === 'double' || fd['type'] === 'long') {
                    sfdItems.push(fd)
                  }
                } else if (dt.recap === 'string') {
                  if (dataType !== 'string') {
                    if (fd['type'] === dataType) {
                      sfdItems.push(fd)
                    }
                  }
                }
                if (fd['type'] === 'string') {
                  sfdItems.push(fd)
                }
              }
            }
          }
        } else {
          // $.extend(sfdItems, enableStoreFd)
        }
        return sfdItems
      }
    },
    props: ['txnId', 'isVisibility'], // 
    data () {
      return {
        toggleIcon: ['el-icon-arrow-up', 'el-icon-arrow-down'], // Table[]Icon
        tranModelList: [],      // 
        expendNodeKey: [],      // 
        tmTitle: '',            // 
        tmDialogVisible: false, // 
        tmForm: {},             // 
        tmTypeList: [],         // -
        allStoreFd: [],         // 
        enableStoreFd: [],      // 
        tmCodeList: [],         // 
        refTab: [],             // 
        refFd: [],              // 
        canRefTab: [],          // 
        canRefTabFd: [],        // 
        func: [],               // 
        funcParam: [],          // 
        tmGenesisRulList: [],   // 
        refTableData: [],       // 
        dataTypeClassify: [     // 
          {recap: 'long', type: ['long', 'time', 'datetime']},
          {recap: 'decimal', type: ['double', 'money']},
          {recap: 'string', type: ['string', 'devid', 'ip', 'userid', 'acc', 'code']}
        ],
        formLabelWidth: '120px'
      }
    },
    mounted: function () {
      this.$nextTick(function () {
        vm = this
      })
    },
    watch: {
      txnId: {
        handler: (val, oldVal) => {
          if (vm.isVisibilityParent === true) {
            vm.initForm()
          }
        }
      },
      isVisibility: {
        handler: (val, oldVal) => {
          if (vm.isVisibilityParent === true) {
            vm.initForm()
          }
        }
      }
    },
    methods: {
      initForm () { // 
        var self = this
        ajax.post({
          url: '/tranmdl/query', //
          param: {
            tab_name: self.txnId
          },
          success: function (data) {
            var tranModelList = data.txnfds
            if (tranModelList) {
              self.tranModelList = tranModelList  // 
              let expendNodeKey = []
              for (let i in tranModelList) {
                for (let key in tranModelList[i]) {
                  expendNodeKey.push(key)         // 
                }
              }
              self.expendNodeKey = expendNodeKey      // 
              self.refTab = data.txn_ref_tab          // 
              self.refFd = data.txn_ref_fds           // 
              self.enableStoreFd = data.enablestorefd // 
              self.allStoreFd = data.allstorefd       // 
            }
            ajax.post({
              url: '/tranmdl/queryOf',//
              param: {
                tab_name: self.txnId
              },
              success: function (data) {
                self.tmCodeList = data.sourcetype // 
                self.canRefTab = data.canreftable // 
                self.canRefTabFd = data.reftblfd  // 
                self.func = data.func             // 
                self.funcParam = data.funcparam   // 
              }
            })
          }
        })
      },
      groupHandle ({ row, column, rowIndex, columnIndex }) {
        if (row.group_type === 'group') { // [group_type][group]
          switch (columnIndex) {          // 
            case 1 :        // 
              return [1, 9] // 
            default :       // 
              return [0, 0] // 
          }
        }
      },
      groupClassName ({row, rowIndex}) { // []
        if (row.group_type === 'group') {
          return 'groupStyle'
        } else {
          return ''
        }
      },
      groupIcon (row) {  // Icon
        if (row.group_type !== 'group') { // 
          return ''                       // 
        }
        let expendNodeKey = this.expendNodeKey      // 
        let key = row.tab_name + '____' + row.name  // 
        if (expendNodeKey.indexOf(key) > -1) {      // 
          return this.toggleIcon[0]
        } else {                                    // 
          return this.toggleIcon[1]
        }
      },
      toggleListHandle (row, e) {   // 
        let self = this
        if (row.group_type !== 'group') { // []
          return
        }
        let path = e.path
        let cellItem = null   // 
        for (let i in path) { // td[cell]
          if (path[i].className === 'cell') {
            cellItem = path[i]
          }
        }
        let key = row.tab_name + '____' + row.name // []
        let expendNodeKey = self.expendNodeKey     // []
        let iconItem = cellItem.getElementsByTagName('i') // I
        if (iconItem[0].getAttribute('class') === 'el-icon-arrow-up') { // []
          if (expendNodeKey.indexOf(key) > -1) {
            expendNodeKey.splice(expendNodeKey.indexOf(key), 1)
          }
        } else if (iconItem[0].getAttribute('class') === 'el-icon-arrow-down') { //
          expendNodeKey.push(key)
        }
        self.expendNodeKey = expendNodeKey
      },
      formatterCode (row, column) {  // table
        let self = this
        if (row.code === undefined || row.code === null || row.code === '') {
          return ''
        } else {
          let codeList = self.enableStoreFd
          for (let i in codeList) {
            if (codeList[i].category_id === row.code) {
              return codeList[i].category_name
            }
          }
          return row.code
        }
      },
      openTmDialog () { // 
        this.tmTypeList = dictCode.getCodeItems('tms.model.datatype') // 
      },
      getTmFdNameList (tabName) { // 
        let self = this
        ajax.post({
          url: '/tranmdl/queryAvailableStoreFd',
          param: {
            tab_name: (tabName?tabName:self.txnId)
          },
          success: function (data) {
            self.enableStoreFd = data.enableStoreFd // 
          }
        })
      },
      tmAddFunc () {  //
        let self = this
        self.tmTitle = ''
        self.tmDialogVisible = true
        self.tmFormReadOnly = false
      },
      tmEditFunc () { // 
        let self = this
        self.tmTitle = ''
        self.tmDialogVisible = true
        self.tmFormReadOnly = false
      },
      tmDelFunc () { // 
        // let self = this
        console.info('')
      },
      tmInfoFunc () { // 
        let self = this
        self.tmTitle = ''
        self.tmDialogVisible = true
        self.tmFormReadOnly = true
      }
    }
  }
</script>

<style>
  .hidden {
    display: none;
  }
  tbody .groupStyle {
    background-color: #f5f7fa !important;
    text-align: left;
  }
  tbody .groupStyle .cell {
    padding-left: 15px;
  }

  .table-header {
    text-align: left;
    line-height: 2em;
    background-color: aquamarine;
  }
</style>



